body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 10s linear;
          animation: App-logo-spin infinite 10s linear;
  height: 7vmin;
  margin-top: 20px;
  position: relative;
  top: -17px;
  right: 10px;
}

.App-link {
  color: #61dafb;
}

a:hover {
  text-decoration: none;
}

h1 {
  font-size: 3.5rem;
  padding: 15px 0;
}

h3 {
  font-size: 1.85rem;
}

p {
  font-size: 1.4rem;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loading {
  width: 200px;
}

footer {
  background-color: #282c34;
  padding: 20px 0;
  margin-top: 20px;
}

footer.absolute-positioned-footer {
  position: absolute;
  bottom: 0px;
  width: 100%;
}

footer small {
  color: #fff;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

.App-header {
    background-color: #282c34;
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-header h1 {
    display: inline-block;
    padding: 0;
}

nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

nav a {
    border: 1px solid #434343;
    padding: 0 10px;
    font-size: 18px;
    border-top: none;
    border-right: none;
    height: 34px;
    line-height: 34px;
    display: inline-block;
}

nav a:hover {
    background-color: #3d4450;
}

nav a:first-of-type {
    border-radius: 0 0 0 20px;
}

nav a:last-of-type {
    border-radius: 0;
}

header a,
header a:visited,
header a:hover,
header a:active,
header a:focus {
    text-decoration: none;
    color: #fff;
}

.login-holder {
    display: flex;
    justify-content: flex-end;
    position: relative;
}

.login-holder input {
    padding: 4px 10px;
    font-size: 14px;
    transition: box-shadow .1s;
    width: 300px;
    border-radius: 0 0 20px 0;
    box-shadow: none;
    border: 1px solid #434343;
    border-top: none;
    border-right: none;
    outline: none;
    color: #1c1c1c;
    height: 34px;
}

.login-holder input:focus,
.login-holder input:hover,
.login-holder input:active {
    transition: box-shadow .1s;
    box-shadow: 0 6px 8px #1c1c1c;
    outline: none;
}

.login-holder .login-button {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 0 0 20px 0;
    background: #17ab93;
    height: 34px;
    width: 54px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.login-holder .login-button:hover,
.login-holder .login-button:active {
    background: #1bc7aa;
}

.login-holder .login-button img {
    height: 20px;
}

@media only screen and (max-width: 576px) {

    .App-header h1 {
        padding: 5px 0;
        font-size: 28px;
    }

    .login-holder input {
        width: 188px;
    }

}

@media only screen and (max-width: 420px) {

    nav {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
    }
    
    nav a {
        width: 50%;
    }
    
    nav a:hover {
        background-color: #3d4450;
    }
    
    nav a:first-of-type {
        border-radius: 0;
    }
    
    .login-holder {
        display: flex;
        justify-content: flex-end;
        position: relative;
    }
    
    .login-holder input {
        border-radius: 0;
        width: 100%;
    }
    
    .login-holder .login-button {
        border-radius: 0;
    }

}
main {
    max-width: 1400px;
    margin: 0 auto;
}

.movieImg {
    height: 260px;
}

.movieBox {
    border: 2px solid #f0f0f0;
    border-radius: 5px;
    margin-bottom: 10px;
    padding-bottom: 15px;
    box-shadow: 0 0 10px -2px #282c34;
    position: relative;
}

.movieBox:hover {
    border: 2px solid #dedede;
}


.moviesHolder h3 {
    padding: 20px 0;
    text-transform: capitalize;
}

.moviesHolder h3.logged-in {
    padding: 30px 0;
}

.moviesHolder img {
    margin: 0 auto;
}

.search-movie {
    font-size: 14px;
    transition: box-shadow .1s;
    width: 50%;
    padding: 8px 5px 8px 16px;
    border-radius: 0 0 18px 18px;
    box-shadow: none;
    border: 1px solid #ccc;
    outline: none;
    position: relative;
    z-index: 3;
    margin-bottom: 10px;
}

.search-movie:focus,
.search-movie:hover,
.search-movie:active {
    transition: box-shadow .1s;
    box-shadow: 0 6px 8px #ccc;
    outline: none;
}

#sortMoviesBtn {
    font-size: 14px;
    margin-bottom: 20px;
    border: 3px solid #282c34;
    border-radius: 25px;
    background-color: #282c34;
    color: #fff;
    padding: 3px 15px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    width: 340px;
    margin: 0 auto 20px;
    position: relative;
}

#sortMoviesBtn:hover {
    background-color: #3d4450;
    border: 3px solid #3d4450;
}

#sortMoviesBtn:focus {
    outline: none;
}

#sortMoviesBtn .selected-filter-title {
    color: #17ab93;
    font-weight: bold;
    position: absolute;
    right: 43px;
}

.sortMovies {
    margin-top: 10px;
}

.sortMovies ul {
    display: table;
    margin: 0 auto;
    padding: 0;
    display: flex;
    justify-content: center;
}

.sortMovies ul li {
    list-style: none;
    /* float: left; */
    margin-right: 15px;
    border: 1px solid #ccc;
    border-radius: 25px;
    background-color: #282c34;
    color: #fff;
    padding: 3px 15px;
    margin-bottom: 10px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.sortMovies ul li:hover {
    background-color: #3d4450;
}

.sortMovies ul li:last-of-type {
    margin-right: 0;
}

.sortMovies ul li.selected-filter {
    background-color: #17ab93;
}

.sortMovies ul li span {
    font-size: 16px;
}


.moviesHolder a {
    text-decoration: none;
    color: #000;
}

.moviesHolder h3 {
    margin-bottom: 0;
}

.image-holder {
    margin: 0 auto;
    display: flex;
    width: 177px;
    height: 260px;
    overflow: hidden;
    justify-content: center;
    align-items: center;
}

.delete-btn {
    padding: 5px 13px;
    border: 1px solid #ccc;
    background-color: #fff;
    color: #000;
    font-weight: normal;
    border-radius: 2px;
    font-size: 12px;
    float: right;
    position: absolute;
    right: 0;
    top: 0;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    border-radius: 0 5px 0 15px;
    border-top: 0px solid #fff;
    border-right: 0px solid #fff;
    transition: all .3s;
}

.delete-btn:hover {
    background-color: #f34f4f;
    color: #fff;
    font-weight: bold;
    padding: 8px 18px;
    transition: all .3s;
}

.single-movie .delete-btn:hover {
    border-radius: 0 0 0 15px;
}

@media only screen and (max-width: 576px) {

    .movieBox,
    .movieBox:hover {
        border: none;
        max-width: 350px;
        margin: 0 auto;
    }
    .single-movie .movieBox,
    .single-movie .movieBox:hover {
        border: none;
        max-width: unset;
    }

    .search-movie,
    #sortMoviesBtn {
        width: 80%;
    }

}

@media only screen and (max-width: 400px) {

    .sortMovies ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        margin: 0 14px;
    }

    #sortMoviesBtn .selected-filter-title {
        right: 10px;
    }

}
form {
    padding: 20px;
}
form label {
    display: block;
}

form input {
    padding: 5px;
}
/* form input[type="text"] {
    margin-bottom: 10px;
    width: 50%;
    border-radius: 5px;
    border: 1px solid gray
} */

form input[type="text"]:last-child {
    margin-bottom: 20px;
}

form input[type="text"], form input[name="name"] {
    font-size: 14px;
    transition: box-shadow .1s;
    width: 50%;
    padding: 8px 5px 8px 16px;
    border-radius: 18px;
    box-shadow: none;
    border: 1px solid #ccc;
    outline: none;
    position: relative;
    z-index: 3;
    margin-top: 5px;
    margin-bottom: 10px;
}

form input[type="text"]:focus,
form input[type="text"]:hover,
form input[type="text"]:active,
form input[name="name"]:focus,
form input[name="name"]:hover,
form input[name="name"]:active {
    transition: box-shadow .1s;
    box-shadow: 0 0 8px #ccc;
    outline: none;
}

input[type="submit"] {
    display: block;
    margin: 0 auto;
    width: 30%;
    background-color: #3fcf6b;
    border: none;
    padding: 5px;
    border-radius: 5px;
    color: #fff;
    font-size: 18px;
}

form input[type="submit"]:hover {
    background-color: #71ff9d;
}

form textarea {
    font-size: 14px;
    transition: box-shadow .1s;
    padding: 8px 5px 8px 16px;
    border-radius: 18px;
    box-shadow: none;
    border: 1px solid #ccc;
    outline: none;
    margin-top: 5px;
    margin-bottom: 10px;

    width: 30%;
    height: 100px;
    margin-bottom: 10px;
}

form input[name="name"] {
    width: 30%;
}

form textarea:focus,
form textarea:hover,
form textarea:active {
    transition: box-shadow .1s;
    box-shadow: 0 0 8px #ccc;
    outline: none;
}

.imagePlaceholder {
    height: 300px;
    max-width: 300px;
    margin: 0 auto;
    border-radius: 8px;
    border: 1px solid #ccc;
    margin-bottom: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden;
}

.imagePlaceholder img {
    max-height: 250px;
}

@media only screen and (max-width: 576px) {
    form input[name="name"], 
    form input[type="text"], 
    form textarea {
        width: 90%;
    }

}
.back-button {
    font-size: 14px;
    display: inline-block;
    margin: 5px;
    padding: 10px 20px;
    border-radius: 25px;
    background: #282c34;
    color: #fff;
}

.back-button:hover {
    background: #3d4450;
    color: #fff;
}

.single-movie h3 {
    font-size: 2.5em;
    padding: 20px 0;
}

.single-movie .movieBox,
.single-movie .movieBox:hover,
.single-movie .movieBox:active,
.single-movie .movieBox:visited {
    width: 100%;
    margin: 0 auto;
    border: none;
    border-radius: 0;
    box-shadow: none;
}

.single-movie .delete-btn {
    padding: 8px 18px;
}

.single-movie .delete-btn:hover {
    padding: 13px 25px;
}

.single-movie .comment-holder {
    border-left: 2px solid #ccc;
    border-right: 2px solid #ccc;
    display: inline-block;
    width: 50%;
    border-radius: 25px;
    padding: 10px 0;
    margin: 10px 0;
}

@media only screen and (max-width: 576px) {
    .single-movie .comment-holder {
        width: 70%;
    }

}
